<template>
  <div class="video-upload-page">
    <div class="title">
      <span></span>
      <span class="tips"> ( app) </span>
    </div>
    <el-form ref="videoForm" label-width="100px" :model="form" class="form" :rules="rules">
      <el-form-item label="" prop="file">
        <el-upload
          ref="upload"
          :accept="'video/*'"
          :on-change="uploadChange"
          :on-exceed="uploadExcced"
          :auto-upload="false"
          :limit="1"
          drag
          action=""
          :multiple="false"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">aviwmvmpegmp4m4vmovasfflvf4v2GB</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="desc">
        <el-input
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 6}"
          placeholder=""
          v-model="form.desc"
          maxlength="50"
          show-word-limit
        >
        </el-input>
      </el-form-item>
      <el-form-item label="" class="song-info">
        <el-form-item prop="songName">
          <el-input placeholder="" v-model="form.songName"></el-input>
        </el-form-item>
        <span> - </span>
        <el-form-item prop="singerName">
          <el-input placeholder="" v-model="form.singerName"></el-input>
        </el-form-item>
      </el-form-item>
    </el-form>
    <el-button :loading="loading" @click="uploadButtonClick"></el-button>
  </div>
</template>

<script>
// @ is an alias to /src
import TcVod from 'vod-js-sdk-v6'
import { reportEvent } from '@/common/utils/helper'
import Header from '@/common/components/Header'
import {
  Input,
  Form,
  FormItem,
  Upload,
  Message,
  Button,
} from 'element-ui'
import {
  fetchSign,
} from '@/api/video'

export default {
  name: 'Home',
  components: {
    Header,
    'el-input': Input,
    'el-form': Form,
    'el-form-item': FormItem,
    'el-upload': Upload,
    'el-button': Button,
  },
  data() {
    return {
      loading: false,
      form: {
        file: null,
        desc: '',
        songName: '',
        singerName: '',
      },
      rules: {
        file: [
          { required: true, message: '', trigger: 'change' },
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' },
        ],
        songName: [
          {validator: (rule, value, callback) => {
            if (value === '' && this.form.singerName !== '') {
              callback('')
            } else {
              callback()
            }
          }, trigger: 'blur'}
        ],
        singerName: [
          {validator: (rule, value, callback) => {
            if (value === '' && this.form.songName !== '') {
              callback('')
            } else {
              callback()
            }
          }, trigger: 'blur'}
        ],
      },
    }
  },
  mounted() {
  },
  methods: {
    getSignature() {
      const f = {
        filesize: this.form.file.size,
        desc: this.form.desc,
        music: this.form.songName ? `${this.form.songName}-${this.form.singerName}` : ''
      }
      return fetchSign(f).then((response) => {
        return response.data.data.sign
      })
    },
    uploadChange(file) {
      const size = file.size
      if (size > 2147483648) {
        Message.error(' 2GB')
        this.$refs['upload'].clearFiles()
        return
      }
      this.form.file = file.raw
    },
    uploadExcced(files, fileList) {
      Message.error('')
    },
    uploadButtonClick() {
      this.$refs['videoForm'].validate((result) => {
        if (result === true) {
          const tcVod = new TcVod({
            getSignature: this.getSignature,
          })
          const uploader = tcVod.upload({
            mediaFile: this.form.file,
          })
          this.loading = true
          Message.success('')
          uploader.on('media_progress', (info) => {
          })
          uploader.done().then((doneResult) => {
            setTimeout(() => {
              this.loading = false
              Message.success('')
              this.$router.push('/profile?index=3')
            }, 3000)
          }).catch((error) => {
            this.loading = false
            Message.error(', ')
          })
        }
      })
    },
  },
}
</script>

